import React from 'react';
import ActiveActivitiesCard from './activeActivitiesCard';
export default class ActiveActivities extends React.Component{
    constructor(props){
        super(props)
    }
    render(){
        return(
            <div>
            <div className="columns">
            
                {this.props.active.map(activeActivity=>{
                    return <ActiveActivitiesCard key={activeActivity} activity={activeActivity}/>
                })}               
            </div>
            <div className="columns">
                {this.props.next.map(activeActivity=>{
                    return <ActiveActivitiesCard key={activeActivity} activity={activeActivity}/>
                })}
            </div>
            </div>
        )   
    }
}